*{
    padding: 0;
    margin: 0;
}
.App{
    margin: 10px;
}
.data{
    width: 400px;
    height: 500px;
    border: 1px solid black;
    border-radius: 10px;
    background-color: black;
}
.header{
    background-color: rgb(122, 248, 237);
    margin: 10px;
    border: 1px solid black;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    padding: 12px;
    gap: 20px;
    
}
.All_contacts{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}
.logo{
    font-size: 18px;
    border: 2px solid white;
    border-radius: 50%;
    color: white;
    font-weight: bold;
    padding: 3px;
    
}
.search_contacts{
    display: flex;
    flex-direction: column;
   
}
.search{
    width: 250px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    text-align: left;
    padding-left: 20px;
}
.element{
    color: white;
}
.container{
    width: 90%;
    margin: 10px auto;
    height: 60px;
    background-color: white;
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
}
.info{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 10px;
    text-align: center;
}
.logo_user{
    height: 40px;
    width: 40px;
    margin-top: 5px;
}
.editOption{
    display: flex;
    flex-direction: row;
    gap: 10px;
    cursor: pointer;
}
.user-details{
    display: flex;
    gap: 10px;
}
.user_info{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.MdAddCircleOutline{
    color: white;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.formcontainer{
 width: 400px;
 height: 300p;
 background-color: white;
 border: 1p solid black;
 margin-top:10px ;
margin-right: 220px;
display: flex;
justify-content: center;
}